body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Three2{
  position: relative;
}
/*  header  */
.header .log{
  width: 50px;
  height: 50px;
  background-color: rgb(255, 255, 255);
  border-radius: 7px;
}
.header{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  z-index: 2;
  background-color: blue;
  border-radius: 0 0 9px 9px;
}
.header ul{
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  list-style: none;
  
}
/*  header  */
/*   body  */
.body{
  position: relative;
}
.start{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 500px;
}
.left ,.right{
  width: 200px;
  height: 300px;
  background-color: blue;
  border-radius: 7px;
}
/*   body  height: 600px !important;*/

.head{
  
  position: absolute;
}
.back{
  position: absolute;
  width: 100%;
  z-index: 1;
}